.widget {
  background-color: #f7f7f7;
  padding: 30px 0;
  &-block {
    border-radius: 10px;
    padding: 15px;
    border: 1px solid #f7f7f7;
    &__head {
      margin-top: 0;
      margin-bottom: 15px;
      text-align: center;
    }
    &__footer {
      margin-top: 30px;
    }
  }
}
.container {
  max-width: 590px;
  margin: 0 auto;
  box-shadow: 0 0 15px rgba(186, 186, 186, 0.25);
  border-radius: 10px;
  padding: 15px;
  background-color: white;
}
.person {
  display: flex;
  align-items: center;
  &__image {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 50px;
    margin-right: 15px;
  }
  &__name {
    margin-top: 0;
    margin-bottom: 6px;
    font-size: 20px;
    font-weight: bold;
  }
}
.footer {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  &__link {
    display: inline-table;
    color: #333;
    text-decoration: none;
    padding: 10px;
    border: 1px solid rgba(51, 51, 51, 0.19);
    border-radius: 5px;
    transition: 0.3s;
    &:hover {
      background-color: #333;
      color: white;
    }
  }
  &__read {
    opacity: 0.5;
  }
}
